@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  /* background: linear-gradient(-135deg, #2CE3AC, #4F4CF0); */
  background: #00CCCC;
  font-family: 'Roboto';
  font-size: 26px;
  color: white;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline;
}

a {
  display: block;
  font-size: 32px;
  min-width: 300px;
  color: white
}

img {
  display: block;
  margin: 0 auto;
}

button {
  /* display: block; */
  /* margin: 0 auto; */
  /* max-width: 175px; */
  /* max-height: 36px; */
  min-width: 125px;
  min-height: 36px;
  font-size: 24px;
  background-color: #f6f05000;
  outline: none;
  border: 4px solid #F6F050;
  border-radius: 10px;
  color: white;
}
